import React from "react"
import PropTypes from "prop-types"
import 'aos/dist/aos.css';
import { TweenMax } from "gsap"

class GiantHeader extends React.Component {

    constructor(props) {
        super(props);
        this.myElement = null;
        this.myTween = null;
    }

    componentDidMount() {
        const AOS = require('aos');
        this.aos = AOS
        this.aos.init()
        this.myTween = TweenMax.to(this.myElement, .25, { x: -100, y: -100, delay: .5 });
    }

    componentDidUpdate() {
        this.aos.refresh()
    }

    render() {
        return (
            <h1 ref={h1 => this.myElement = h1} css={{
                fontFamily: "Open Sans",
                fontWeight: '700',
                fontSize: this.props.fontsize,
                textTransform: 'uppercase',
                WebkitTextStroke: this.props.stroke,
                textStroke: this.props.stroke,
                color: this.props.color,
                opacity: this.props.opacity
            }}>{this.props.text}</h1>)
    }



}

GiantHeader.propTypes = {
    text: PropTypes.string

}

GiantHeader.defaultProps = {
    fontsize: '250px'
}

export default GiantHeader
